import { createStore } from 'vuex';

const store = createStore({
    state() {
        return {
            name: 'Your Name',
            introduction: 'A brief introduction about yourself.',
            expertise: ['JavaScript', 'Vue.js', 'CSS', 'HTML'],
            portfolioItems: [
                {
                    id: 1,
                    title: 'Project 1',
                    description: 'Description for project 1',
                    image: 'path/to/image1.jpg',
                    link: 'https://example.com/project1',
                },
                {
                    id: 2,
                    title: 'Project 2',
                    description: 'Description for project 2',
                    image: 'path/to/image2.jpg',
                    link: 'https://example.com/project2',
                },
                // Add more portfolio items as needed
            ],
        };
    },
    mutations: {
        setName(state, name) {
            state.name = name;
        },
        setIntroduction(state, introduction) {
            state.introduction = introduction;
        },
        addExpertise(state, expertise) {
            state.expertise.push(expertise);
        },
        removeExpertise(state, expertise) {
            state.expertise = state.expertise.filter(item => item !== expertise);
        },
        addPortfolioItem(state, item) {
            state.portfolioItems.push(item);
        },
        removePortfolioItem(state, itemId) {
            state.portfolioItems = state.portfolioItems.filter(item => item.id !== itemId);
        },
        updatePortfolioItem(state, updatedItem) {
            const index = state.portfolioItems.findIndex(item => item.id === updatedItem.id);
            if (index !== -1) {
                state.portfolioItems.splice(index, 1, updatedItem);
            }
        },
    },
    getters: {
        getName(state) {
            return state.name;
        },
        getIntroduction(state) {
            return state.introduction;
        },
        getExpertise(state) {
            return state.expertise;
        },
        getPortfolioItems(state) {
            return state.portfolioItems;
        },
    },
});

export default store;
